* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
   font-family: 'Averia Serif Libre', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wather_app{
  width: 300px;
  height: 350px;
  margin: 100px auto 0;
  text-align: center;
  border-radius: 15px;
  position: relative;
  z-index: 2;
  background-image: linear-gradient(to top, #9890e3 0%, #b1f4cf 100%);
}
.wather_app .input{
  margin-top: 40px;
  padding: 5px;
  height: 30px;
  outline: none;
  border-radius: 13px;
}
.wather_app .info{
  margin-top: 35px;
  padding-bottom: 10px;
}
.wather_app .info .H1{
  display: flex;
  justify-content: center;
  text-transform: capitalize;
}
.wather_app .info .H2{
  margin: 15px 0;
  text-transform: capitalize;
}
.wather_app .info .H4{
  padding-top: 20px;
}
.wather_app .info p{
  padding-top: 35px;
}
.cliod{
  position: absolute;
  top: 0;
  margin-top: 120px;
  margin-left: 120px;
  z-index: -1;
  color: #fff;
}
.cliod .one{
  display: flex;
  justify-content: space-between;
  width: 145px;
}
.cliod .two{
  display: flex;
  justify-content: space-between;
  width: 197px;
  margin: 42px 0 0 -105px;
}
.cliod .three{
  display: flex;
  justify-content: space-between;
  width: 253px;
  margin: 56px 0 0 -92px;
}

